import React, { Component } from 'react';
//state
import styles from './css/Footer.css';

class Footer extends Component {
    
   
    render() {

        return (
            <div>
                <div className={styles.footer_main}>
                    <div className={`${styles.footer_center} ${this.props.pub.clearfix}`}>
                <ul>
                    <h3>京诚概览<i>&#xe606;</i></h3>
                    <li>京诚简介</li>
                    <li>发展历程</li>
                    <li>经营成果</li>  
                </ul>
                <ul>
                    <h3>企业公民<i>&#xe606;</i></h3>
                    <li>企业文化</li>  
                    <li>社会责任</li>
                    <li>社会褒奖</li>
                </ul>
                <ul>
                    <h3>服务保障<i>&#xe606;</i></h3>
                    <li>资质认证</li>   
                    <li>核心团队</li>
                    <li>质量安全</li>
                    <li>标准</li>
                </ul>
                <ul>
                    <h3>研发中心<i>&#xe606;</i></h3>
                    <li>研发创新体系</li>   
                    <li>研发机构</li>
                    <li>科技成果</li>
                    <li>专利</li>
                </ul>
                <ul>
                    <h3>加盟京诚<i>&#xe606;</i></h3>
                    <li>加盟京诚</li>  
                    <li>招聘岗位</li>
                    <li>校园</li>
                </ul>
                <ul>
                    <h3>新闻中心<i>&#xe606;</i></h3>
                    <li>京诚新闻 </li>
                    <li>媒体</li>
                </ul>
                <ul>
                    <h3>研发中心<i>&#xe606;</i></h3>
                    <li>联系我们</li>   
                    <li>视频中心</li>
                    <li>宣传册下载</li>
                    <li>问卷调查</li>
                </ul>
                </div>
                </div>
                <div className={styles.copy_foot}>
                <div className={styles.copy_center}>
                <div className={styles.left_font}>
                Copyright © 2017 中冶京诚工程技术有限公司 All Rights Reserved.
                </div>
                <div className={styles.right_list}>
                <a href='##'>
                    友情链接
                </a>  
                 |         
                <a href='##'>
                    网站地图
                </a>
                |   
                <a href='##'>
                    纪检监察
                </a>
                </div>
                </div>
                </div>
            </div>)
    }
}

export default Footer



