
import React,{Component} from 'react';
import styles from './css/profile.css';

class Profile extends Component{
    render(){
        return (
            <div className={styles.profile_wrap}>
                <div className={styles.profile}>
                    <div className={styles.title}>
                        <h4>京诚简介</h4>
                        <p>CERI PROFILE</p>
                        <span></span>
                    </div>
                    <div className={`${styles.con} ${this.props.name.clearfix}`}>
                        <div className={styles.con_padding}>
                            <div className={styles.con_left}></div>
                            <div className={styles.con_text}>
                                <p>
                                    中冶京诚工程技术有限公司公司注册（以下简称“中冶京诚”）成立于1951年由中国冶金工业部原作为中国第一个国家冶金设计院。 中冶京诚是全球最大的工程公司的中冶集团，世界500强公司在世界内。
                                    <span></span>
                                    中冶京诚是一个完整的解决方案提供商，采矿和金属，基础设施，建筑工程和可再生能源与服务，从咨询，工程设计，设备制造和供应，项目管理，工程总承包和融资服务。
                                    <span></span>
                                    中冶京诚已成功委托中国和主要国际市场，如独联体，美国，东南亚，印度，中东和北非地区6000多个项目。
                                </p>
                                <a href='#' className={styles.btn}><i>&#xe606;</i>查看更多</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Profile;


