import React,{Component} from "react";
import styles from './css/css.css'
class New extends Component{
    render(){
        return(
            <div className={styles.news}>
                <div className={this.props.name.concent+' '+this.props.name.clearfix}>
                    <div className={styles.title}>
                        <h2>新闻中心</h2>
                        <p>News Center</p>
                        <span></span>
                    </div>
                    <ul className={this.props.name.concent+' '+styles.newsList+' '+this.props.name.clearfix}>
                        <li>
                            <div className={styles.img}></div>
                            <span>2017. 04. 13</span>
                            <p>CERI授予华金PL-TCM项目</p>
                        </li>
                        <li>
                            <div className={styles.img}></div>
                            <span>2017. 04. 13</span>
                            <p>强盐水再利用系统授予创新示范项目</p>
                        </li>
                        <li>
                            <div className={styles.img}></div>
                            <span>2017. 04. 13</span>
                            <p>荣获冶金建筑行业优秀工程软件一等奖</p>
                        </li>
                        <li>
                            <div className={styles.img}></div>
                            <span>2017. 04. 13</span>
                            <p>CERI签署了塞浦路人长草庄园项目EPC合作 框架协议</p>
                        </li>
                    </ul>
                    <a className={styles.more}><i>&#xe606;</i>查看更多</a>
                </div>
            </div>
        )
    }
}
export default New


