
import React,{Component} from 'react';
class Nav extends Component{
    render(){
        return (
            <div className={this.props.name.hd_right}>
                <ul className={this.props.name.nav}>
                    <li><span>京诚概览</span> <i>&#xe600;</i></li>
                    <li><span>企业公民</span> <i>&#xe600;</i></li>
                    <li><span>服务保障</span> <i>&#xe600;</i></li>
                    <li><span>业务产品</span> <i>&#xe600;</i></li>
                    <li><span>研发中心</span> <i>&#xe600;</i></li>
                    <li><span>加盟京诚</span> <i>&#xe600;</i></li>
                    <li><span>新闻中心</span> <i>&#xe600;</i></li>
                    <li><span>为您服务</span> <i>&#xe600;</i></li>
                </ul>
                <ul className={this.props.name.hd_lang}>
                    <li>
                        English <span>&#xe604;</span>
                    </li>
                    <li>
                        中文 <span>&#xe604;</span>  
                    </li>   
                </ul>
                <button>
                    &#xe634;
                </button>
            </div>
        )
    }
}
export default Nav;


